.products {
  box-sizing: border-box !important;
  width: 100%;
  padding: 20px;
  box-sizing: border-box;
}

.prod-block {
  width: 100%;
  height: auto;
  box-shadow: 0px 0px 10px rgba(0,0,0,0.08);
  box-sizing: border-box;
  padding: 15px;
  border-radius: 10px;
  margin-bottom: 20px;
}

.prod-img {
  width: 100%;
  height: 180px;
  border-radius: 10px;
  box-sizing: border-box;
}

.prod-price-and-buy {
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: space-between;
  box-sizing: border-box;
}


.prod-buy-btn {
  background-color: #ff6700;
  box-sizing: border-box;
  padding: 5px 25px;
  box-shadow: 0 3px 15px rgba(255,103,0,0.35);
}
